* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --color-131921: #131921;
  --color-3b4149: #3b4149;
  --color-febd69: #febd69;
  --color-232f3e: #232f3e;
  --color-bf4800: #bf4800;
  --color-f5f5f7: #f5f5f7;
  --color-ededed: #ededed;
  --color-777777: #777777;
  --color-1c1c1b: #1c1c1b;
}
.section-heading {
  font-size: 26px;
  line-height: 32px;
  font-weight: 500;
  margin-bottom: 30px;
}
.card-wrapper {
  background-color: white;
  box-shadow: 0 0 10px #0000001a;
  padding: 15px;
}
body {
  font-family: "Rubik", sans-serif;
}
a {
  text-decoration: none !important;
  display: inline-block;
}
.form-control {
  box-shadow: none;
  border-color: inherit;
}
.gap-5 {
  gap: 5px;
}
.gap-10 {
  gap: 10px;
}
.gap-15 {
  gap: 15px;
}
.gap-30 {
  gap: 30px;
}
.gap-60 {
  gap: 60px;
}
.button {
  background-color: var(--color-232f3e);
  color: white;
  font-size: 14px;
  padding: 13px 33px;
  border-radius: 15px;
}
.button:hover {
  background-color: var(--color-febd69);
  color: var(--color-131921);
}
/* Header start */

.header-top-strip {
  
  background-color: var(--color-131921);
}
.header-upper {
  background-color: var(--color-131921);
}
.header-upper .input-group-text {
  background-color: var(--color-febd69);
}
.header-upper.header-upper-links img {
  width: 35px;
  height: 35px;
}
.header-bottom {
  background-color: var(--color-232f3e);
}
.header-bottom .menu-links a,
.header-bottom .dropdown button {
  color: white;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400px;
  letter-spacing: 0.3;
  text-transform: uppercase;
}

.header-bottom .dropdown button:focus {
  box-shadow: none;
}
.header-bottom .dropdown-menu {
  background-color: var(--color-131921);
  width: 100%;
  transform: translate3d(0px, 57px, 0px) !important;
}
.header-bottom .dropdown-item {
  padding: 20px;
  margin-bottom: 3px;
  
}
.header-bottom .dropdown-item:hover {
  background-color: transparent;
  color: var(--color-febd69) !important;
}
/* Header end */
/* Footer start */
footer {
  background-color: var(--color-232f3e);
}
footer:not(:first-child) {

}
footer .input-group-text {
  background-color: var(--color-232f3e);
  color: white;
  padding: 8px 17px;
}
/* Footer end */
/* Home start */
.main-banner-contetnt {
  top: 15%;
  left: 5%;
}
.main-banner-contetnt h4 {
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color: var(--color-bf4800);
  margin: 0 0 12px;
  letter-spacing: 0.3px;
}
.main-banner-contetnt h5 {
  font-size: 52px;
  font-weight: 500;
  line-height: 64px;
  letter-spacing: -2px;
}
.main-banner-contetnt p {
  font-size: 18px;

  line-height: 28px;
  color: var(--color-131921);
  letter-spacing: 0.4;
}

.small-banner-contetnt {
  top: 15%;
  left: 5%;
}
.small-banner-contetnt h4 {
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  color: var(--color-bf4800);
  margin: 0 0 12px;
  letter-spacing: 0.3px;
}
.small-banner-contetnt h5 {
  font-size: 22px;
  font-weight: 500;
  line-height: 34px;
  letter-spacing: -2px;
}

.small-banner {
  width: 49%;
}
.small-banner-contetnt p {
  font-size: 14px;

  line-height: 22px;
  color: var(--color-131921);
  letter-spacing: 0.4;
}
/* home wrapper 2 */
.home-wrapper-2 {
  background-color: var(--color-f5f5f7);
}
/* home wrapper 2 end */
/* categories section */
.categories {
  background-color: white;
  box-shadow: 0 0 10px #0000001a;
  padding: 15px;
}
.categories > div {
  width: 25%;
  padding: 10px 10px 10px 20px;
}
.categories > div > div {
  width: 60%;
}
.categories > div:nth-child(1),
.categories > div:nth-child(2),
.categories > div:nth-child(3),
.categories > div:nth-child(4) {
  border-bottom: 1px solid var(--color-ededed);
}
.categories > div:nth-child(1),
.categories > div:nth-child(2),
.categories > div:nth-child(3) {
  border-right: 1px solid var(--color-ededed);
}
.categories > div:nth-child(5),
.categories > div:nth-child(6),
.categories > div:nth-child(7) {
  border-right: 1px solid var(--color-ededed);
}
/* categories section end */
/* Home end */
/* Blog part */
.blog-card {
  background-color: white;
  padding: 20px;
  border-radius: 10px;
}
.blog-card img {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.blog-content p.date {
  font-size: 15px;
  line-height: 24px;
  text-transform: uppercase;
  letter-spacing: 0;
  font-weight: 400;
  color: var(--color-777777);
  padding: 0;
}
.blog-content h5 {
  font-size: 18px;
  color: var(--color-131921);
}
.blog-content p.desc {
  font-size: 13px;
  line-height: 22px;
  color: var(--color-777777);
}
.single-blog-card h3 {
  font-size: 24px;
  font-weight: 600;
  color: var(--color-1c1c1b);
}
.single-blog-card p {
  font-size: 14px;
  color: var(--color-777777);
}
.single-blog-card a {
  color: var(--color-777777);
  font-size: 18px;
}

/* Blog part end */
/* Product  card */
.product-card {
  padding: 15px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 0 10px #0000001a;
  overflow: hidden;
  width: 100%;
}
.product-card .product-image img {
  object-fit: contain;
  height: 296px;
  transition: 0.3s;
}
.product-card .product-image > img:last-child {
  display: none;
}
.product-card:hover .product-image > img:nth-child(1) {
  display: none;
  transition: 0.3s;
}
.product-card:hover .product-image > img:nth-child(2) {
  display: block;
  transition: 0.3s;
}

.product-card:hover .action-bar {
  right: 10px;
}
.product-card .product-details h6 {
  color: var(--color-bf4800);
  font-size: 13px;
}
.product-card .product-details h5 {
  font-size: 15px;
  color: var(--color-1c1c1b);
}
.product-card .product-details p.price {
  font-size: 16px;
  color: var(--color-1c1c1b);
}
.product-card .product-details p.description {
  font-size: 13px;
  color: var(--color-777777);
  margin-right: 20px;
}
.action-bar {
  top: 20%;
  right: -30px;
  transition: 1s;
}
.wishlist-icon {
  top: 10%;
  right: 10px;
}
/* Product card end */

/* Special product */
.special-product-card {
  padding: 20px 10px;
  border-radius: 10px;
  background-color: white;
}
/* Special product end */

/* famous card */
.famous-card .famous-content {
  top: 5%;
  left: 5%;
}
.famous-card img {
  border-radius: 10px;
}
.famous-card .famous-content h5 {
  font-size: 13px;
  line-height: 20px;
  font-weight: 400px;
  color: white;
  margin-bottom: 7px;
}
.famous-card .famous-content h6 {
  font-size: 24px;
  line-height: 38px;
  font-weight: 500;
  color: white;
}
.famous-card .famous-content p {
  font-size: 16px;
  line-height: 24px;
  font-weight: 100;
  color: white;
}
/* famous card end */

/* our store */
.filter-card {
  background-color: white;
  border-radius: 10px;
  padding: 15px 15px 25px;
}
.filter-tittle {
  font-size: 16px;
  line-height: 20px;
  font-weight: 600px;
  color: var(--color-1c1c1b);
  margin-bottom: 20px;
}
.filter-card ul,
.filter-card .form-check-label {
  list-style: none;
  font-size: 13px;
  line-height: 30px;
  color: var(--color-777777);
  cursor: pointer;
}
.filter-card .form-check-input:focus {
  box-shadow: none;
  border-color: rgba(0, 0, 0, 0.25);
}
.filter-card .form-check-input:checked {
  background-color: var(--color-febd69);
  border-color: var(--color-febd69);
}
.sub-tittle {
  font-size: 14px;
  font-weight: 600;
  color: var(--color-1c1c1b);
  margin-bottom: 25px;
  margin-top: 25px;
}
.colors {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
.colors li {
  height: 20px;
  width: 20px;
  background-color: brown;
  border-radius: 50%;
  border: 1px solid var(--color-131921);
 text-align: center;
}

.random-products h5 {
  font-size: 14px;
  margin-bottom: 8px;
}
.random-products:first-child {
  border-bottom: 1px solid var(--color-ededed);
}
.filter-sort-grid {
  padding: 10px;
  background-color: white;
  border-radius: 10px;
}
.grid img {
  width: 35px;
  height: 35px;
  background-color: #f2f2f2f2;
  padding: 8px;
  border-radius: 3px;
  cursor: pointer;
}
.gr-12 {
  width: 100%;
}
.gr-6 {
  width: 49%;
}
.gr-4 {
  width: 32%;
}
.gr-3 {
  width: 24%;
}
.gr-12 .product-card {
  display: flex;
  gap: 20px;
}
.gr-6 .product-image {
  width: 80%;
}
.gr-6 .product-image img {
  margin: auto;
  display: block;
}
.gr12 .product-card .product-image {
  width: 100%;
  height: auto;
}

/* our store end */

/* Contact page*/
.contact-wrapper-inner {
  padding: 30px 20px;
  border-radius: 15px;
  background-color: white;
  gap: 15px;
}
.contact-wrapper-inner > div {
  width: 48%;
}
.contact-tittle {
  font-size: 26px;
  line-height: 35px;
  font-weight: 500;
  text-align: left;
}
.contact-wrapper-inner form .form-control {
  background-color: var(--color-f5f5f7);
  border-radius: 10px;
  border-color: transparent;
  padding: 10px;
}
.contact-wrapper-inner ul {
  list-style: none;
}
/* Contact page end */

/* Compare page */
.compare-product-card {
  padding: 20px 15px;
  background-color: white;
  border-radius: 10px;
}
.compare-product-details h5.tittle,
.wishlist-card h5.title {
  font-size: 15px;
  line-height: 22px;
  font-weight: 500;
  color: var(--color-1c1c1b);
}
.compare-product-details h6.price,
.wishlist-card h6.price {
  font-size: 15px;
  line-height: 22px;
  font-weight: 400;
}
.compare-product-card .product-detail {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0px;
  border-top: 1px solid var(--color-f5f5f7);
}
/* Compare page end */

/* Auth */
.auth-card{
  padding: 20px;
  background-color: white;
  width: 500px;
  margin: 30px auto;
  border-radius: 10px;
}
.auth-card h3{
  font-size: 19px;
  font-weight: 500;
  color: var(--color-777777);
}
.auth-card .form-control{
  height: 50px;
  background-color: var(--color-f5f5f7);
  color: var(--color-777777);
  font-size: 14px;
  border-radius: 10px;
  border-color: transparent;

}
.auth-card .signup{
  background-color: var(--color-febd69);
  color: var(--color-131921);
}
.auth-card .signup:hover{
  background-color: var(--color-131921);
  color: white;
}
.auth-card p{
  font-size: 13px;
  color: var(--color-777777);
}
/* Auth end */

/* Policy page */
.policy{
  background-color: white;
  padding: 30px;
}
/* Policy end */

/* main product */
  .product-image{
    padding: 20px;
    background-color: white;
    border-radius: 10px;
  }
  .product-image> div{
    border: 1px solid rgba(0, 0, 0, 0.25);
  }
 .orther-product-image{
  background-color: white;
  padding: 20px;
 }
 .orther-product-image > div{
  padding: 20px;
  width: 48%;
  border: 1px solid rgba(0, 0, 0, 0.25);
 }
 .main-product-details{
  background-color: white;
  padding: 20px;
  border-radius: 10px;
 }
 .border-bottom{
  border-bottom:1px solid rgba(0, 0, 0, 0.25);
  margin-top: 20px;
 }
  .main-product-details h3 .tittle{
    font-size: 18px;
   
    font-weight: 600;
    color: var(--color-1c1c1b);
  }
  .main-product-details p .price{
    font-size: 16px;
    font-weight: 400;
    color: var(--color-777777);
  }
/* main product end */
/* cart */
.cart-header h4{
  font-size: 13px;
  color: var(--color-777777);
}
.cart-header{
  border-bottom: 1px solid #eaeaea;
}
.cart-col-1{
  width: 40%;
}
.cart-col-2{
  width: 10%;
}
.cart-col-3{
  width: 15%;
}
.cart-col-4{
  width: 15%;
}
/* cart end */